<template>
    <div class="search-panel theme-background-default">
        <div class="scroll-wrapper">
            <!--<div class="navigation-list">-->
            <!--<div class="navigation-list-item" v-for="item in searchList" @click="handlerNavigationItem(item)">-->
            <!--<div style="margin-bottom: 9px">-->
            <!--<mu-avatar size="45"-->
            <!--:style="`padding: 6px;`"-->
            <!--:color="item.typeId | formatFind(icons, 'color', 'typeId')">-->
            <!--<img style="padding: 6px" :src="item.typeId | formatFind(icons, 'value', 'typeId')">-->
            <!--</mu-avatar>-->
            <!--</div>-->
            <!--<span>{{item.value}}</span>-->
            <!--</div>-->
            <!--</div>-->
            <div class="history-wrapper">
                <mu-list class="pn" v-if="historyList.length">
                    <mu-list-item class="border-1px-b" :key="item.query"
                                  @click.native="handlerHistoryItem(item, index)"
                                  v-for="(item, index) in historyList">
                        <mu-list-item-action>
                            <mu-avatar color="transparent" size="16">
                                <img :src="require('../../../static/icon/search_bar.png')">
                            </mu-avatar>
                        </mu-list-item-action>
                        <mu-list-item-content>
                            <mu-list-item-title>{{item.query}}</mu-list-item-title>
                        </mu-list-item-content>
                        <mu-list-item-action>
                            <mu-icon @click.stop="handlerRemove(item)" color="grey" value="close"/>
                        </mu-list-item-action>
                    </mu-list-item>
                    <mu-list-item button>
                        <mu-button :ripple="false" @click="handlerClear" class="w" flat>清空历史记录</mu-button>
                    </mu-list-item>
                </mu-list>
                <no-success height="80vh" text="还没有留下历史数据" v-else/>
            </div>
        </div>
    </div>
</template>

<script>
    import {MapMixins} from "../../common/js/mixins"
    import {appMixins, searchMixins} from "../../common/mixins"
    import NoSuccess from "../../base/no-success/no-success"

    export default {
        name: "search-panel",
        mixins: [appMixins, searchMixins, MapMixins],
        data() {
            return {
                list: []
            }
        },
        computed: {
            historyList() {
                return this.history.map(item => {
                    return JSON.parse(item)
                })
            }
        },
        methods: {
            // 移除历史纪录
            handlerRemove(target) {
                target = JSON.stringify(target)

                this.deleteSearchHistory(target)
            },
            // 清空历史纪录
            handlerClear() {
                this.clearSearchHistory()
            },
            // 点击定位
            handlerLocation(item) {
                this.$toast({
                    message: '定位中 ...',
                    duration: 0
                })

                this.setCenter(item.x, item.y).then(res => {
                    this.$toast({
                        message: '定位成功',
                        duration: 300,
                    })

                    this.$router.push('/')
                }).catch(err => {
                    this.$toast({
                        message: '定位失败',
                        duration: 500
                    })
                })
            },
            // 点击历史纪录
            handlerHistoryItem(item) {
                this.$emit('change', item.query)

                // if (this.$route.query.id && item.id) {
                //     this.$router.replace({
                //         name: 'search',
                //         query: {
                //             id: item.id
                //         }
                //     })
                //
                //     return
                // }

                // if (item.id) {
                //     this.$router.push({
                //         name: 'search',
                //         query: {
                //             id: item.id
                //         }
                //     })
                // }
            },
            handlerNavigationItem(item) {
                const id = this.$route.query.id

                this.$emit('change', '')

                if (id) {
                    this.$router.replace({
                        name: 'search',
                        query: {
                            id: item.typeId
                        }
                    })
                } else {
                    this.$router.push({
                        name: 'search',
                        query: {
                            id: item.typeId
                        }
                    })
                }
            }
        },
        filters: {
            formatElement(id) {
                const result = this.searchList.find(item => {
                    return item.typeId === id
                })

                return result ? result.value : ''
            }
        },
        components: {
            NoSuccess
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"
    @import "../../common/stylus/mixins.styl"

    .search-panel
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        z-index 1
        padding-top 70px
        overflow hidden

        .history-wrapper
            margin 0 13px
            border-radius 5px
            overflow hidden
</style>



