<template>
    <div class="views search">
        <div class="search-wrapper paper shadow">
            <transition mode="in-out" name="fade">
                <div @click="back" class="back" v-if="!$route.query.id">
                    <mu-icon size="30" value="navigate_before"/>
                </div>
                <div :style="`background-color: ${target.color}`" @click="back" class="badge" v-else>
                    <img :data-id="target.id" :src="target.icon" class="badge-image">
                    <div class="badge-inner">
                        <span>{{target.name}}</span>
                    </div>
                    <mu-icon color="#fff" size="20" value="close"/>
                </div>
            </transition>
            <div class="input-wrapper">
                <field class="input" placeholder="搜索船名、航标、桥梁、水位站 ……" ref="field" v-model="query"/>
            </div>
        </div>
        
        <transition name="views-down">
            <search-panel @change="change" v-if="animation"/>
        </transition>
        
        <transition name="views-down">
            <div class="result-wrapper theme-background-default" v-show="query">
                <result :query="query"/>
            </div>
        </transition>
        
        <transition name="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
    </div>
</template>

<script>
    import Result from './result'
    import SearchPanel from './search-panel'
    import {appMixins, searchMixins} from "../../common/mixins"
    
    const SEARCH = 'SEARCH'
    
    export default {
        name: SEARCH,
        mixins: [appMixins, searchMixins],
        data() {
            return {
                query: '',
                animation: false,
                searchLoading: false
            }
        },
        computed: {
            target() {
                const id = this.$route.query.id
                
                const icon = this.icons.find(item => {
                    return item.typeId === id
                })
                
                const item = this.searchList.find(item => {
                    return item.typeId === id
                })
                
                return {
                    color: icon ? icon.color : '',
                    icon: icon ? icon.value : '',
                    name: item ? item.value : '',
                    id
                }
            }
        },
        activated() {
            this.animation = true
            
            this.query = ''
        },
        deactivated() {
            this.animation = false
        },
        methods: {
            change(value) {
                this.query = value
                
                this.$refs.field.$refs.input.focus()
            },
            back() {
                // if (this.query) {
                //     this.query = ''
                // }
                
                this.$router.replace('/')
            }
        },
        components: {
            Result,
            SearchPanel
        }
    }
</script>

<style lang="stylus">
    .search
        padding-top 50px
        overflow hidden
        
        > .search-wrapper
            position absolute
            top 0
            left 0
            right 0
            z-index 999
            display flex
            align-items center
            margin 10px 13px
            padding 0 10px
            height 50px
            background-color #fff
            border-radius 5px
            overflow hidden
            transition all .5s
            
            .badge
                display flex
                align-items center
                justify-content center
                padding-right 5px
                height 25px
                border-radius 25px
                
                .badge-image
                    display block
                    padding 4px
                    width 25px
                    height 25px
                
                .badge-inner
                    padding 0 3px
                    height 25px
                    line-height 25px
                    color #fff
                    font-size 14px
            
            .input-wrapper
                flex 1
        
        > .result-wrapper
            position absolute
            top 0
            left 0
            right 0
            bottom 0
            z-index 100
</style>



