<template>
    <div class="result-list" v-loading="queryLoading">
        <div class="scroll-wrapper result-container">
            <mu-load-more :loading="loading" v-if="list.length" @load="onLoad">
                <mu-list class="pn">
                    <mu-list-item class="border-1px-b"
                                  v-for="(item, index) in list" :key="item.id"
                                  @click.native="handlerItem(item, index)">
                        <mu-list-item-action style="min-width: 30px">
                            <mu-avatar size="22" color="transparent">
                                <img :src="item.typeId | formatFind(searchIcons, 'value', 'typeId')"
                                     :data-id="item.typeId">
                            </mu-avatar>
                        </mu-list-item-action>
                        <mu-list-item-title class="no-wrap" style="padding-right: 1em">
                            {{item.name}}
                        </mu-list-item-title>
                        <mu-list-item-action style="min-width: 30px">
                            <mu-button color="#2196f3" @click.stop="collection(item)"
                                       v-loading="item.loading"
                                       :disabled="item.loading"
                                       small flat>
                                设为常用
                            </mu-button>
                        </mu-list-item-action>
                    </mu-list-item>
                </mu-list>
            </mu-load-more>
            <no-success text="暂无搜索结果" height="100%" v-else/>
        </div>
    </div>
</template>

<script>
    import {debounce} from "../../types/index"
    import {getSearchList} from "../../api/search"
    import {saveOrUpdateIAddress} from "../../api/personal"
    import NoSuccess from "../../base/no-success/no-success"
    import {searchMixins, userMixins} from "../../common/mixins"
    import {isArray} from "../../types/index"

    export default {
        name: "result-list",
        mixins: [userMixins, searchMixins],
        data() {
            return {
                loading: false,
                list: [],
                params: {
                    name: '',
                    // page: 1,
                    pageNumber: 1,
                    pageSize: 20
                },
                queryLoading: false
            }
        },
        props: {
            query: {
                type: String,
                default: ''
            }
        },
        computed: {
            id() {
                return this.$route.query.id
            }
        },
        created() {
            this.$watch('query', debounce(value => {
                this.queryLoading = true

                this.params.name = value

                if (!value) {
                    this.list = []
                    
                    return
                }

                this.getSearchList()
            }, 300))
        },
        activated() {
            this.list = []
        },
        methods: {
            // 点击收藏
            collection(item) {
                item.loading = true

                const data = {
                    name: item.name,
                    longitude: item.longitude,
                    latitude: item.latitude
                }

                if (!this.isLogin) {
                    return this.$toast('您需要登录才能操作!')
                }

                saveOrUpdateIAddress(data).then(res => {
                    item.loading = false

                    this.$toast('添加常用地址成功')
                }).catch(err => {
                    item.loading = false

                    this.$toast('添加常用地址失败')
                })
            },
            // 请求搜索信息
            getSearchList() {
                this.params.pageNumber = 1
                // this.params.page = 1

                // if (this.id) {
                //     this.params.typeId = this.id
                //     getNavigationList(this.params).then(res => {
                //         this.list = isArray(res)
                //     })
                // } else {
                getSearchList(this.params).then(res => {
                    this.list = isArray(res)

                    this.queryLoading = false
                })
                // }
            },
            // 加载
            onLoad() {
                // this.params.page++
                this.params.pageNumber++
                this.loading = true

                // if (this.id) {
                //     this.params.typeId = this.id
                //     getNavigationList(this.params).then(res => {
                //         this.loading = false
                //         this.list.push(...isArray(res))
                //     })
                // } else {
                getSearchList(this.params).then(res => {
                    this.loading = false
                    this.list.push(...isArray(res))
                })
                // }
            },
            // 点击
            handlerItem(item) {
                const data = {
                    query: this.query
                }

                this.$router.push({
                    path: '/search',
                    query: data
                })

                this.saveSearchHistory(JSON.stringify(data))

                this.$router.push({
                    name: 'search-detail',
                    params: {
                        type: item.subType || item.typeId || this.$route.query.id,
                        id: item.id
                    },
                    query: {
                        typeId: item.typeId
                    }
                })
            }
        },
        watch: {
            id(value) {
                this.getSearchList()
            }
        },
        components: {
            NoSuccess
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"

    .result-list
        padding-top 70px
        width 100%
        height 100%
        
        .result-container
            margin 0 13px
            width auto
            border-radius 5px
            box-shadow 0 0 30px -20px rgba(0, 0, 0, .5)
            
            >>> .mu-item
                padding-right 5px

        .text
            font-size $font-size-sm
            text-align right
            color #BEBEBE

            .d
                color #48AED9
</style>



